@import "./base/normalize";

@font-face {
    font-family: 'Larsseit-Bold';
    src: url('./fonts/Larsseit-Bold.woff2') format('woff2');
    font-weight: 700;
    font-display: swap;
}

@font-face {
    font-family: 'Larsseit-ExtraBold';
    src: url('./fonts/Larsseit-ExtraBold.woff') format('woff');
    font-weight: 900;
    font-display: swap;
}

@font-face {
    font-family: 'Larsseit-Medium';
    src: url('./fonts/Larsseit-Medium.woff2') format('woff2');
    font-weight: 500;
    font-display: swap;
}

@font-face {
    font-family: 'Larsseit';
    src: url('./fonts/Larsseit.woff2') format('woff2');
    font-weight: 400;
    font-display: swap;
}



html, body {
    font-family: "Larsseit", sans-serif;
}

.container {
    display: grid;
    justify-content: center;
    max-width: 1440px;
    margin: 0 auto;
    grid-template-columns: repeat(3, 33%);
    grid-auto-rows: minmax(80px, auto);
}

//header //

.header {
    background: #f4f9e2;
    grid-area: 1/1/1/4;
    padding: 20px 126px 70px 180px;
    position: relative;
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 185px;
        z-index: 5;
    }
    &__link {
        font-family: Larsseit;
        font-size: 24px;
        line-height: 27px;
        letter-spacing: -0.035em;
        color: #62d0df;
    }
    &__btn-top {
        width: 150px;
        height: 50px;
        left: 1159px;
        top: 27px;
        color: #fff;
        background: #62d0df;
        border-color: transparent;
        border-radius: 50px;
        z-index: 5;
    }
    &__img {
        width: 50%;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
    }
    &__content {
        display: flex;
    }
    &__wrapper {
        max-width: 496px;
    }
    &__title {
        margin-bottom: 75px;
        font-size: 48px;
        line-height: 53px;
        letter-spacing: -0.035em;
        color: #22356f;
    }
    &__text {
        margin-bottom: 65px;
        line-height: 42px;
        color: #22356f;
        font-size: 24px;
        line-height: 42px;
    }
    &__btn-wrapper-act {
        padding: 10px 30px;
        font-size: 16px;
        line-height: 23px;
        color: #fff;
        background: #f9b640;
        border-color: transparent;
    }
    &__btn-wrapper {
        padding: 10px 35px;
        margin-right: 25px;
        font-size: 16px;
        line-height: 23px;
        color: #f9b640;
        background: transparent;
        border-color: transparent;
    }
}

.nav__list {
    display: flex;
    justify-content: space-between;
}

.nav__item {
    margin-right: 15px;
    list-style-type: none;
    
}

.nav__link {
    font-size: 14px;
    line-height: 16px;
    text-decoration: none;
    color: #22356f;
}


//  whytherive //

.whytherive {
    display: flex;
    justify-content: space-between;
    grid-area: 2/1/2/4;
    background: #62d0df;
    &__left {
        padding: 115px 130px 95px 180px;
    }
    &__title {
        margin-bottom: 95px;
        font-size: 36px;
        line-height: 27px;
        color: #ffffff;
    }
    &__text {
        font-size: 20px;
        line-height: 30px;
        color: #ffffff;
    }
    &__s {
        font-size: 24px;
        line-height: 35px;
        color: #ffffff;
    }
}

